* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #001a4d 0%, #003d99 100%);
  color: #333;
  min-height: 100vh;
}

html, body, #root {
  width: 100%;
  height: 100%;
}

/* Variables de color */
:root {
  --color-blue: #001a4d;
  --color-blue-light: #003d99;
  --color-red: #cc0000;
  --color-red-light: #ff3333;
  --color-white: #ffffff;
  --color-gray: #f5f5f5;
  --color-text: #333;
}
